// cards
.cards {
	&__list {
		display: flex;
		justify-content: space-between;
		color: $white;
		flex-wrap: wrap;
		@include sm {
			justify-content: space-around; }
		@include xs {
			justify-content: center;
			flex-direction: column;
			align-items: center; } }
	&__item {
		margin-top: 53px;
		flex-shrink: 0;
		flex-grow: 0;
		width: 31.33%;
		max-width: 31.33%;
		background-color: gray;
		border-radius: 10px;
		@include cover;
		@include sm {
			width: 44%;
			max-width: 44%; }
		@include xs {
			width: 100%;
			max-width: 290px;
			margin-top: 30px; }
		&:first-of-type {
			background-image: url(../img/card/et.jpg); }
		&:nth-of-type(2) {
			background-image: url(../img/card/lite.jpg); }
		&:last-of-type {
			background-image: url(../img/card/bitc.jpg); } } }
.card {
	padding: 20px 30px;
	@include xs {
		padding: 15px 20px; }
	&__row {
		display: flex;
		justify-content: space-between;
		align-items: center;
		margin-top: 5px; }
	&__title {
		color: $gray; }
	&__price {
		font-size: 20px; }
	&__stat {
		color: $green;
		font-size: 16px;
		@include tr(color .3s ease);
		&.is-red {
			color: $red;
			span {
				display: none; } } }
	&__header {
		text-align: center;
		font-size: 22px; }
	&__body {
		padding-top: 143px; }
	&__toggle {
		display: flex;
		justify-content: center;
		align-items: center; } }
.card__row-percent {
	margin: 13px 0; }
.card__title-wh,
.card__title-wh {
	color: $white; }
.card__title-price {
	font-size: 20px; }
