.switch__field {
  height: 0;
  width: 0;
  visibility: hidden;
}

.switch__field:checked + label {
  background: gray;
}

.switch__field:checked + label:after {
  left: 100%;
  transform: translate(-100%, -50%);
}

.switch__label {
  cursor: pointer;
  text-indent: -9999px;
  width: 42px;
  box-shadow: inset 1px 3px 7px rgba(27, 36, 40, 0.56);
  height: 16px;
  background: #3eb5f1;
  display: block;
  border-radius: 8px;
  position: relative;
}

.switch__label:after {
  content: '';
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  left: 0px;
  width: 20px;
  height: 20px;
  background: #fff;
  border-radius: 50%;
  transition: 0.3s;
}

.switch__label:active:after {
  width: 30px;
}

