  background-image: url(../img/card/et.jpg);
}

.cards__item:nth-of-type(2) {
  background-image: url(../img/card/lite.jpg);
}

.cards__item:last-of-type {
  background-image: url(../img/card/bitc.jpg);
}

.card {
  padding: 20px 30px;
}

@media only screen and (max-width: 599px) {
  .card {
    padding: 15px 20px;
  }
}

.card__row {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: 5px;
}

.card__title {
  color: rgba(255, 255, 255, 0.2);
}

.card__price {
  font-size: 20px;
}

.card__stat {
  color: #70c446;
  font-size: 16px;
  transition: color 0.3s ease;
}

.card__stat.is-red {
  color: #c80e24;
}

.card__stat.is-red span {
  display: none;
}

.card__header {
  text-align: center;
  font-size: 22px;
}

.card__body {
  padding-top: 143px;
}

.card__toggle {
  display: flex;
  justify-content: center;
  align-items: center;
}

.card__row-percent {
  margin: 13px 0;
}

.card__title-wh,
.card__title-wh {
  color: #fff;
}

.card__title-price {
  font-size: 20px;
}
